import {getBrowser} from "@/utils/browser-util";

let WAPLUS_SENDER_INSTALL_URL;
let WAPLUS_CRM_INSTALL_URL;


let browserType = getBrowser().browser
if(browserType === 'Chrome'){
    WAPLUS_SENDER_INSTALL_URL  = "https://chrome.google.com/webstore/detail/waplus-sender-wa-message/lbjgmhifiabkcifnmbakaejdcbikhiaj?utm_source=initialize_advertisement"
    WAPLUS_CRM_INSTALL_URL = "https://chrome.google.com/webstore/detail/waplus-crm-simple-whatsap/jmjcgjmipjiklbnfbdclkdikplgajhgc?utm_source=initialize_advertisement";
}else if(browserType === 'Edge'){
    WAPLUS_SENDER_INSTALL_URL  = "https://microsoftedge.microsoft.com/addons/detail/waplus-sender-wa-messag/cjnhajfljdmnkmkgnehmfopjljmmplla"
    WAPLUS_CRM_INSTALL_URL = "https://microsoftedge.microsoft.com/addons/detail/waplus-crm-simple-whats/nhkkbpphaankeiolhcpmaemibkamionn";
}




export {
    WAPLUS_SENDER_INSTALL_URL,
    WAPLUS_CRM_INSTALL_URL
}
