import {WAPLUS_CRM_INSTALL_URL, WAPLUS_SENDER_INSTALL_URL} from "@/config/config";
console.log('注入content成功')
let myNumber
window.onload = async function () {
    try {
        let data;
        if (window.localStorage.getItem('last-wid')) {
            data = window.localStorage.getItem('last-wid')
            myNumber = data.split('@')[0].substring(1);
        } else {
            data = window.localStorage.getItem('last-wid-md')
            myNumber = data.split(':')[0].substring(1);
        }
        // 给background传递用户手机号，用来给GA日志传递信息
        chrome.runtime.sendMessage({userPhoneNum: myNumber});
        chrome.storage.local.set({userPhoneNum: myNumber});
    } catch (e) {
        // console.log('获取本地手机号出错');
    }
};

chrome.storage.sync.get([
    'on',
    'currentPopupMessage',
    'messages',
    'messagesPreview',
    'mediaPreview',
    'mediaGallery',
    'textInput',
    'profilePic',
    'name',
    'noDelay',
    'unblurActive'
  ], function(data) {
    function removeStyleById(id){
      // check if the stylesheet is there before removing
        if((el=document.getElementById(id))){
            el.parentNode.removeChild(el);
        }
    }

    function addStyle(id){
      // only add stylesheets if they weren't added yet
        if(!document.getElementById(id)){
            var link = document.createElement('link');
            link.id = id;
            link.className = 'pfwa';
            link.href = chrome.runtime.getURL('css/'+id+'.css');
            link.type = 'text/css';
            link.rel = 'stylesheet';
            document.getElementsByTagName('head')[0].appendChild(link);
        }
    }

    // add stylesheets if selected otherwise remove it
    if(data.on) {
        if (data.messages) addStyle('messages'); else removeStyleById('messages');
        if (data.messagesPreview) addStyle('messagesPreview'); else removeStyleById('messagesPreview');
        if (data.mediaPreview) addStyle('mediaPreview'); else removeStyleById('mediaPreview');
        if (data.mediaGallery) addStyle('mediaGallery'); else removeStyleById('mediaGallery');
        if (data.textInput) addStyle('textInput'); else removeStyleById('textInput');
        if (data.profilePic) addStyle('profilePic'); else removeStyleById('profilePic');
        if (data.name) addStyle('name'); else removeStyleById('name');
        if (data.noDelay) addStyle('noDelay'); else removeStyleById('noDelay');
        if (data.unblurActive) addStyle('unblurActive'); else removeStyleById('unblurActive');
    }else if(document.getElementsByClassName('pfwa').length>0){
        // remove all stylesheets
        var el = document.getElementsByClassName('pfwa');
        while(el.length > 0){
            el[0].parentNode.removeChild(el[0]);
        }
    }
});

chrome.runtime.onMessage.addListener(async function (data, sender, sendResponse) {
    let openChatPhoneNum = data.openChat;
    if (openChatPhoneNum) {
        let condition = await openChat(openChatPhoneNum);
        if (condition) {
            await sendText("Hi privacy!I have a question.", "input");
        }
    }
})

// 打开聊天窗口--检验并打开聊天窗口
function openChat(phoneNum) {
    // chat_people_name = document.querySelector("#main > header > div._24-Ff > div._2rlF7 > div > span")
    return new Promise((resolve) => {
        openChatUrl(phoneNum).then(() => {
            setTimeout(async function () {
                let condition = await hasOpened();
                resolve(condition);
            }, 3000);
        });
    });
}

// 打开聊天窗口--通过当前页面有没有小弹窗来确定是否已经
async function hasOpened() {
    return new Promise(resolve => {
        setTimeout(async function () {
            let condition = true;
            if (document.querySelector('[data-animate-modal-popup="true"]')) {
                condition = false;
                document.querySelector('[class="tvf2evcx m0h2a7mj lb5m6g5c j7l1k36l ktfrpxia nu7pwgvd gjuq5ydh"]').click()
            }
            resolve(condition);
        }, 1000)
    })
}

// 打开聊天窗口--打开指定联系人的一个聊天窗口
function openChatUrl(phoneNum) {
    return new Promise((resolve) => {
        let wam = document.getElementById("wamessages");
        if (!wam) {
            wam = document.createElement("a");
            wam.id = "wamessages";
            document.body.append(wam);
        }
        wam.setAttribute(
            "href",
            `https://api.whatsapp.com/send?phone=${phoneNum}`
        );
        // Wait for DOM operation to complete.
        setTimeout(() => {
            wam.click();
            resolve();
        }, 0);
    });
}

// 发送文本--把文本显示在聊天框的发送框中
async function sendText(e, type) {
    if (e) {
        // 向文本框输入信息
        let messageBox = document.querySelectorAll("[contenteditable='true']")[1];
        let event = document.createEvent("UIEvents");
        // 需要添加一个innerHTMl来执行事件
        messageBox.innerHTML = e.replace(/ /gm, " ");
        event.initUIEvent("input", !0, !0, window, 1);
        messageBox.dispatchEvent(event);
        if (type === 'send') {
            await eventFire(document.querySelector('span[data-icon="send"]'), "click");
        }
    }
}

